.card {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: rgba(249, 249, 249, 1);
    margin: 40px 0px;
}

.card:first-of-type {
    margin-top: 70px;
}
.card:last-of-type {
    margin-bottom: 0;
}

@media screen and (max-width: 850px) {
    .card {
        margin: 40px 0px;
    }
}

@media screen and (max-width: 500px) {
    .card {
        margin: 20px 0px;
        flex-direction: column-reverse;
    }
}

.card__info {
    display: flex;
    flex-direction: column;
    margin-left: 22px;
}

@media screen and (max-width: 500px) {
    .card__info {
        margin-top: 14px;
        flex-direction: row;
        justify-content: space-between;
        margin-left: 12px;
        margin-right: 16px;
    }
}

.card__title {
    max-width: 350px;
    max-height: 22px;
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    margin: 0;
    margin-top: 22px;
    margin-bottom: 12px;
	overflow: hidden;
}

@media screen and (max-width: 650px) {
    .card__title {
        font-size: 13px;
    }
}

@media screen and (max-width: 500px) {
    .card__title {
        font-size: 12px;
        margin: 0;
        margin-bottom: 8px;
    }
}

.card__duration {
    font-size: 11px;
    font-weight: 400;
    line-height: 13px;
    margin: 0;
    margin-bottom: 30px;
}

@media screen and (max-width: 500px) {
    .card__duration {
        margin-bottom: 12px;
    }
}

.card__like {
    border: none;
    width: 14px;
    height: 12px;
    box-sizing: border-box;
    box-shadow: none;
    padding: 0;
}

.card__like_active {
    background: url(../../images/like_active.svg) no-repeat center;

}

.card__like_unactive {
    background: url(../../images/like_unactive.svg) no-repeat center;
}

.card__like_delete {
    background: url(../../images/delete-card.svg) no-repeat center;
}

.card__like:hover {
    cursor: pointer;
}

.card__poster {
    width: 220px;
    height: 122px;
    padding: 6px;
}

@media screen and (max-width: 500px) {
    .card__poster {
        padding: 0;
        display: block;
        margin: 0 auto;
        width: 300px;
        height: 168px;
    }
}