.register__logo {
  margin: 0;
}

@media screen and (max-width: 540px) {
  .register__logo {
    display: block;
    margin: 0 auto;
  }
}

.register__container {
  margin: auto;
  margin-top: 70px;
  max-width: 396px;
  min-height: 330px;
}

@media screen and (max-width: 850px) {
  .register__container {
    margin-top: 232px;
    margin-bottom: 232px;
  }
}

@media screen and (max-width: 540px) {
  .register__container {
    margin-top: 56px;
    margin-bottom: 30px;
    max-width: 260px;
  }
}

.register__title {
  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
  margin: 0;
  margin-bottom: 20px;
  margin-top: 40px;
}

@media screen and (max-width: 540px) {
  .register__title {
    text-align: center;
    margin-bottom: 80px;
  }
}

.register__signin {
  display: flex;
  flex-direction: row;
  margin: 0;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
}

.register__login-link {
  text-decoration: none;
  color: rgba(160, 160, 160, 1);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 17px;
}

@media screen and (max-width: 850px) {
  .register__login-link {
    font-size: 12px;
    font-weight: 400;
    line-height: 15px;
  }
}

.register__login-link:last-child {
  color: rgba(43, 224, 128, 1);
  margin-left: 5px;
}

.register__label {
  font-size: 10px;
  font-weight: 400;
  line-height: 12px;
  color: rgba(160, 160, 160, 1);
}

.register__input {
  min-width: 46px;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  padding-top: 13px;
  padding-bottom: 13px;
  width: 100%;
  color: black;
  background: #f6f6f6;
  border: 1px solid #cecece;
  border-radius: 8px;
  margin-top: 10px;
}

.register__input:last-of-type {
  margin-bottom: 0;
}

.register__button {
  background: rgba(43, 224, 128, 1);
  color: rgba(255, 255, 255, 1);
  border: none;
  border-radius: 2px;
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  margin-bottom: 16px;
  margin-top: 69px;
  width: 100%;
  min-height: 50px;
  padding: 0;
}

@media screen and (max-width: 440px) {
  .register__button {
    margin-top: 157px;
    font-size: 12px;
    font-weight: 500;
    line-height: 15px;
  }
}

.register__button:disabled {
  cursor: none;
  background: rgba(43, 224, 128, 0.5);
}


.register__errors {
  display: flex;
  flex-direction: row;
  margin-bottom: 15px;
}

.register__errors:last-of-type {
  margin-bottom: 0;
}

.register__error {
  margin-right: 5px;
  margin-bottom: 5px;
  color: rgba(238, 52, 101, 1);
  font-size: 10px;
  font-weight: 400;
  line-height: 12px;
}
